import Api from '../utils/Api';
import { EMAIL_LOGIN_TOKEN, HEADER, FOOTER } from './actionTypes';

export const fetchEmailLoginToken = () => async (dispatch) => {
  try {
    const emailLoginToken = await Api.contactEmailLogin();
    dispatch({ type: EMAIL_LOGIN_TOKEN, payload: emailLoginToken });
  } catch (err) {
    console.error(err);
  }
};

export const storeHeader = (comp) => ({ type: HEADER, payload: comp });

export const storeFooter = (comp) => ({ type: FOOTER, payload: comp });
