import React, { useEffect } from "react";
import {
  /*HashRouter*/ BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";
import {
  MuiThemeProvider, // MaterialUI developer bug: use unstable createMuiTheme below
  /* createMuiTheme, */ unstable_createMuiStrictModeTheme as createMuiTheme,
  Typography,
  Link as MatLink,
} from "@material-ui/core";
import { StylesProvider } from "@material-ui/core/styles";
import { useDispatch } from "react-redux";
import styles from "./App.module.css";
import Home from "../Pages/Home/Home";
import Company from "../Pages/Company/Company";
import Contact from "../Pages/Contact/Contact";
import Menu from "../Menu/Menu";
import { storeHeader, storeFooter } from "../../actions/actions";
import Portfolio from "../Pages/Portfolio/Portfolio";
import Workshop from "../Pages/Workshop/Workshop";
import Error from "../Pages/Error/Error";
import Linkedin from "../../img/linkedin.svg";
import Facebook from "../../img/facebook.svg";
import Twitter from "../../img/twitter.svg";
import Logo from "../../img/logo.svg";

/**
 * Customize Material UI settings
 * - added Google Fonts
 * - customized Primary color
 */
const theme = createMuiTheme({
  typography: {
    fontFamily: "'IBM Plex Mono', monospace",
  },
  palette: {
    primary: {
      main: "rgb(97, 97, 97)",
    },
  },
});

const App = () => {
  const header = () => (
    <header className={styles.header}>
      <Link to="/">
        <img className={styles.logoimg} src={Logo} alt="logo" />
      </Link>
    </header>
  );

  const footer = () => {
    const linkConfig = [
      {
        url: process.env.REACT_APP_LINKEDIN_LINK,
        source: Linkedin,
        alt: "linkedin",
      },
      {
        url: process.env.REACT_APP_FACEBOOK_LINK,
        source: Facebook,
        alt: "facebook",
      },
      {
        url: process.env.REACT_APP_TWITTER_LINK,
        source: Twitter,
        alt: "twitter",
      },
    ];
    return (
      <footer className={styles.footer}>
        <div>
          {linkConfig.map((social, i) => (
            <MatLink
              key={i.toString()}
              href={social.url}
              target="_blank"
              rel="noreferrer"
              underline="none"
            >
              <img
                className={styles.socialicons}
                src={social.source}
                alt={social.alt}
              />
            </MatLink>
          ))}
        </div>
        <Typography
          style={{
            fontSize: ".7rem",
            padding: "2rem",
            textAlign: "center",
          }}
        >
          Copyright © 2021 4Front Software Development LLC. All rights reserved
        </Typography>
      </footer>
    );
  };

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(storeHeader(header()));
    dispatch(storeFooter(footer()));
  }, [dispatch]);

  const routeList = () => {
    const routes = [
      {
        path: "/",
        exact: true,
        component: () => <Home />,
      },

      {
        path: "/company",
        component: () => <Company />,
      },

      {
        path: "/portfolio",
        component: () => <Portfolio />,
      },
      {
        path: "/workshop",
        component: () => <Workshop />,
      },
      {
        path: "/contact",
        component: () => <Contact />,
      },
      {
        path: "/*",
        component: () => <Error />,
      },
    ];

    return routes;
  };

  return (
    <main className={styles.app}>
      {/* Providers below override Material UI styles using CSS files */}
      <MuiThemeProvider theme={theme}>
        <StylesProvider injectFirst>
          <Router>
            <Menu />
            <Switch>
              {routeList().map((route, index) => (
                <Route
                  key={index.toString()}
                  path={route.path}
                  exact={route.exact}
                >
                  {route.component}
                </Route>
              ))}
            </Switch>
          </Router>
        </StylesProvider>
      </MuiThemeProvider>
    </main>
  );
};

export default App;
