import axios from 'axios';

export default class Api {
   static contactEmailLogin = async () => {
     const header = {
       headers: {
         username: process.env.REACT_APP_NODEMAILER_FAKE_USERNAME,
         password: process.env.REACT_APP_NODEMAILER_FAKE_PASSWORD,
       },
     };

     const response = await axios.post(
       `${process.env.REACT_APP_PROD_EMAIL_ROOT}${process.env.REACT_APP_PROD_EMAIL_LOGIN_ROUTE}`,
       {},
       header,
     );
     return response.data.token;
   }

    static sendContactEmail = async (inputs, emailLoginToken) => {
      const header = {
        headers: {
          Authorization: `Bearer ${emailLoginToken}`,
        },
      };

      const response = await axios.post(
        `${process.env.REACT_APP_PROD_EMAIL_ROOT}${process.env.REACT_APP_PROD_EMAIL_SEND_ROUTE}`,
        inputs,
        header,
      );
      return response;
    }
}
