import React from 'react';
import { useSelector } from 'react-redux';
import {
  Typography, Grid, Paper,
} from '@material-ui/core';
import styles from './Portfolio.module.css';
import PortfolioData from '../../../utils/PortfolioData';


const Portfolio = () => {
  const store = useSelector((state) => state.reducers);
  return (
    <div className={styles.portfolio}>
      {store.header}
      <Typography
        align="center"
        variant="h4"
        style={{ padding: '1rem' }}
      >
        Portfolio
      </Typography>
      { PortfolioData.map((entry, i) => (
        <Paper
          className={styles.paper}
          key={i.toString()}
        >
          <Grid
            container
            style={{ padding: '1rem' }}
          >
            <Grid
              item
              xs={12}
              md={2}
              container
              justify="center"
              alignItems="center"
            >
              <img
                src={entry.img}
                alt={entry.altImg}
                style={{ width: '8rem', borderRadius: '4px' }}
              />
            </Grid>
            <Grid item xs={12} md={10} container>
              <Typography style={{ fontSize: '.8rem', padding: '1rem' }}>
                {entry.text}
              </Typography>
            </Grid>
          </Grid>
        </Paper>
      ))}
      {store.footer}
    </div>
  );
};

export default Portfolio;
