import { EMAIL_LOGIN_TOKEN, HEADER, FOOTER } from '../actions/actionTypes';

export default (reducers = {}, action) => {
  switch (action.type) {
    case EMAIL_LOGIN_TOKEN:
      return { ...reducers, emailLoginToken: action.payload };
    case HEADER:
      return { ...reducers, header: action.payload };
    case FOOTER:
      return { ...reducers, footer: action.payload };
    default:
      return reducers;
  }
};
